<template>
  <form v-on:submit.prevent="sendComment">
    <b-input v-model="message" />
    <b-button block type="submit">new comment</b-button>
  </form>
</template>

<script>
import { api } from '@/const';

export default {
  name: 'CommentEditor',
  data() {
    return {
      message: '',
    };
  },
  computed: {
    userId() { return this.$store.state.userId; },
  },
  methods: {
    sendComment() {
      fetch(`${api}/editor/comments/`, {
        method: 'post',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          drawing: this.id,
          author: this.userId,
          comment: this.message,
        }),
      }).then((resp) => {
        if (resp.status === 201) {
          this.$emit('callrefresh');
        }
      });
    },
  },
  props: ['id'],
};
</script>
