<template>
    <b-form v-on:submit.prevent="handleLogin">
        <b-row>
            <h2>What's your name?</h2>
            <b-input v-model="username" required />
            <b-button block type="submit">
                Login
            </b-button>
        </b-row>
    </b-form>
</template>

<script>
import { mapMutations } from 'vuex';
import { api } from '../const';

const USER_API = `${api}/editor/users`;


export default {
  name: 'login',
  data() {
    return {
      username: '',
    };
  },
  methods: {
    handleLogin() {
      const processResponceData = (d) => {
        this.setUserId(d.id);
        this.setUser(d.nick_name);
      };

      fetch(`${USER_API}/${this.username}`).then(
        (r) => {
          if (r.ok) {
            r.json().then(processResponceData);
          } else {
            fetch(`${USER_API}/`, {
              method: 'POST',
              headers: {
                'Content-Type': 'application/json',
              },
              body: JSON.stringify({
                nick_name: this.username,
              }),
            }).then(rr => rr.json()
              .then(processResponceData));
          }
        },
      );
      this.setUser(this.username);
    },
    ...mapMutations([
      'setUserId',
      'setUser',
    ]),
  },
};
</script>
