<template>
  <section>
    <b-row>
      <b-col>
        <h2>Explore</h2>
      </b-col>
      <b-col>
        <b-button
                block
                class="float-right"
                v-on:click="refresh"
        >
          refresh
        </b-button>
      </b-col>
    </b-row>
    <b-card-group v-for="item in items">
      <item-preview v-bind:item="item" :key="item.id" />
    </b-card-group>
  </section>
</template>

<script>
import { mapMutations } from 'vuex';
import { api } from '../../const';
import ItemPreview from './Preview.vue';

export default {
  name: 'Explore',
  components: { ItemPreview },
  computed: {
    loading() { return this.$store.state.explorer.loading; },
    items() { return this.$store.state.explorer.items; },
  },
  methods: {
    refresh() {
      fetch(`${api}/editor/drawings/`).then((resp) => {
        if (resp.status === 200) {
          resp.json()
            .then((d) => {
              this.refreshExplore(d);
            });
        }
      });
    },
    ...mapMutations([
      'refreshExplore',
    ]),
  },
  updated() {
    this.$nextTick(() => {
      if (window.chemwriter) {
        window.chemwriter.refresh();
      }
    });
  },
  mounted() {
    this.refresh();
  },
};
</script>

<style scoped>
  section {
      min-height: 20em;
      width: 100%;
  }
</style>
