<template>
  <b-card v-bind:title="item.title" class="mb-2">
    <b-row>
      <b-col md="6" class="preview">
        <div
          :id="imageId"
          :data-chemwriter-data="item.data.file"
          data-chemwriter-ui="image"/>
      </b-col>
      <b-col md="6">
        <b-container fluid>
          <b-row>
            Created at: {{ item.creation_date }}
          </b-row>
          <b-row>
            Author: {{ item.author_name }}
          </b-row>
          <b-row class="text-left">
            Description: <br />
            {{ item.description }}
          </b-row>
          <b-row>
            <b-btn block v-on:click="editCopy">load copy</b-btn>
          </b-row>
        </b-container>
      </b-col>
    </b-row>
    <comment-loop :drawingid="item.id" />
  </b-card>
</template>
<script>
import { mapMutations } from 'vuex';
import CommentLoop from "../comments/CommentLoop";

export default {
  name: 'item-preview',
  components: {CommentLoop},
  props: ['item'],
  data() {
    return {
      comments: [],
    };
  },
  computed: {
    imageId: {
      get() {
        return `image-${this.item.id}`;
      },
    },
  },
  methods: {
    editCopy() {
      // TODO: Move this stuff to vuex
      window.chemwriter.components.editor.setMolfile(this.item.data.file);
    },
    ...mapMutations([
      'updateDraftTitle',
      'updateDraftDescription',
      'updateDraftMolfile',
    ]),
  },
  updated() {
    const image = window.chemwriter.components[this.imageId];
    image.setMolfile(this.item.data.file);
  },
};
</script>

<style>
  .preview {
    height: 18em;
  }
</style>
