<template>
  <b-container fluid>
    <b-alert :show="dismissCountDown"
             dismissible
             variant="success"
             @dismissed="dismissCountDown=0"
             @dismiss-count-down="countDownChanged">
      <p>Saved</p>
      <b-progress variant="success"
                  :max="dismissSecs"
                  :value="dismissCountDown"
                  height="4px">
      </b-progress>
    </b-alert>
    <b-row class="collaborator">
      <b-col md="8" lg="8">
        <div class="editor">
          <pulse-loader v-if="loading" />
          <div id="editor" data-chemwriter-ui="editor" v-else></div>
        </div>
      </b-col>
      <b-col md="4" lg="4">
        <b-form v-on:submit.prevent="save">
          <b-row>
            <label>title</label>
            <b-input required v-model="title"/>
          </b-row>
          <b-row class="description">
            <label>description</label>
            <b-textarea v-model="description" required/>
          </b-row>
          <b-row>
            <b-button-group class="btn-block">
              <b-btn v-on:click="clean" type="button">New</b-btn>
              <b-btn type="submit">Save</b-btn>
            </b-button-group>
          </b-row>
        </b-form>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import { mapMutations } from 'vuex';

import PulseLoader from 'vue-spinner/src/PulseLoader.vue';

import { api } from '../../const';

/**
 * hook for update chemwriter
 */
const makeMeGood = () => {
  if (window.chemwriter) {
    window.chemwriter.refresh();
  }
};

function getEditor() {
  if (window.chemwriter) {
    return window.chemwriter.components.editor;
  }
  return null;
}

export default {
  name: 'editor',
  components: {
    PulseLoader,
  },
  data() {
    return {
      loading: true,
      mustBeCleaned: false,
      saved: true,
      notChanged: false,
      dismissSecs: 4,
      dismissCountDown: 0,
    };
  },
  computed: {
    userId: {
      get() {
        return this.$store.state.userId;
      },
    },
    molfile: {
      get() {
        return this.$store.state.draft.data;
      },
    },
    title: {
      get() {
        return this.$store.state.draft.title;
      },
      set(value) {
        this.notChanged = false;
        this.updateDraftTitle(value);
      },
    },
    description: {
      get() {
        return this.$store.state.draft.description;
      },
      set(value) {
        this.notChanged = false;
        this.updateDraftDescription(value);
      },
    },
  },
  methods: {
    clean() {
      this.mustBeCleaned = true;
      this.notChanged = true;
      this.loading = false;
      this.cleanDraft();

      getEditor().setMolfile(this.molfile);
    },

    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    },

    save() {
      fetch(`${api}/editor/drawings/`, {
        method: 'post',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          author: this.userId,
          title: this.title,
          description: this.description,
          data: {
            file: this.molfile,
          },
        }),
      }).then((responce) => {
        if (responce.status <= 299) {
          this.notChanged = true;
          this.showAlert();
        }
      });
    },
    ...mapMutations([
      'updateDraftTitle',
      'updateDraftMolfile',
      'updateDraftDescription',
      'cleanDraft',
    ]),
  },
  mounted() {
    // must be arrow fn for storing context
    const startLoader = () => {
      if (!window.chemwriter) {
        const chemwriter = document.createElement('script');
        chemwriter.setAttribute(
          'src',
          'https://chemwriter.com/sdk/chemwriter.js',
        );
        chemwriter.onload = () => {
          this.loading = false;
        };
        document.head.appendChild(chemwriter);
      } else {
        this.loading = false;
        makeMeGood();
      }
    };

    startLoader();
  },
  updated() {
    this.$nextTick(() => {
      // spoke for reload all chemwriter components
      // after mounting node
      makeMeGood();

      /* eslint-disable-next-line */
      const editor = getEditor();

      editor.addEventListener(
        'document-edited',
        () => {
          this.updateDraftMolfile(editor.getMolfile());
        },
      );
    });
  },
};
</script>

<style scoped>
  .collaborator {
    margin: 1em auto;
    height: 100%;
  }

  .editor {
    width: 100%;
    min-height: 500px;
    height: 100%;
    margin-bottom: 1em;
  }

  .description {
    flex: 1;
  }

  textarea {
    height: 100%;
    box-sizing: border-box;
  }

  form {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
</style>
