<template>
  <div>
    <h4>Comments</h4>
    <comment-editor :id="drawingid" v-on:callrefresh="updateComments"/>
    <div>
      <b-list-group v-for="item in comments" :key="item.id">
        <comment :item="item" />
      </b-list-group>
    </div>
  </div>
</template>

<script>
import CommentEditor from './CommentEditor.vue';
import Comment from './Comment';

import { api } from '@/const';


export default {
  name: 'CommentLoop',
  components: { Comment, CommentEditor },
  data() {
    return {
      comments: [],
    };
  },
  props: ['drawingid'],
  methods: {
    updateComments() {
      fetch(`${api}/editor/comments/?drawing=${this.drawingid}`)
        .then((resp) => {
          if (resp.ok) {
            resp.json().then((d) => {
              this.comments = d;
            });
          }
        });
    },
  },
  mounted() {
    this.updateComments();
  },
};
</script>
